body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron{
    background: url(/static/media/books.09b8ff05.jpg) right bottom;
    background-size: cover;
    color:white;
    font-weight: bolder;
    text-align: center;
    padding:0;
    height: 300px;
}

.title {
    background-color: rgba(0, 0, 0, .2);
    height: 300px;
}

.title h1 {
    padding-top:5%;
}
ul.search-results img {
    margin: 0 auto;
  }
.bookResult {
    /* border: 1px solid lightgray; */
    padding:5px;
    margin-bottom:10px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;

}
.row {
    margin:0;
    padding: 0;
}
nav.navbar.navbar-default {
    margin-bottom: 0;
  }
.footer {
    min-height: 100px;
    background: #E5E5E5;
    position: relative;
  }
  
  .footer span {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 3px;
    background: #B8B8B8;
  }
.wrapper {
    min-height: 100%;
    padding-bottom: 100px;
  }
  
